import p5 from 'p5'

class BrokenPeices {
  constructor(p, position) {
    this.p = p
    this.position = p.createVector(
      position.x + p.random(-30, 30),
      position.y + p.random(-20, 20)
    )
    this.lifespan = p.random(100, 255)
    this.r = p.random(1, 3)
    // this.velocity = p5.Vector.random2D()
    // this.acceleration = p.createVector(0, 0)
  }

  show() {
    //this.velocity.add(this.acceleration)
    //this.position.add(this.p.createVector(this.p.random(5), this.p.random(5)))

    this.p.stroke(this.lifespan)
    this.p.rect(this.position.x, this.position.y, this.r * 2, this.r * 2)
    this.lifespan += 0.05
  }
}

export default BrokenPeices
