import React, { Component } from 'react'
import p5 from 'p5'

import ParticleSystem from './ParticleSystems'
import BrokenPeices from './BrokenPeices'

class Home extends Component {
  constructor() {
    super()
    this.myP5 = null
    this.canvasRef = React.createRef()

    this.sketch = (p) => {
      let particles = []
      let broken = []

      p.setup = () => {
        let divInfo = document
          .getElementById('home-wrapper')
          .getBoundingClientRect()
        let divW = divInfo.width
        let divH = divInfo.height
        p.createCanvas(divW, divH).parent(this.canvasRef.current)
        //p.frameRate(1)
        for (let i = 0; i < 50; i++) {
          let ps = new ParticleSystem(p)
          particles.push(ps)
        }

        //console.log(particles)
      }

      p.draw = () => {
        p.background(255)
        for (let i = 0; i < particles.length; i++) {
          particles[i].update()
          particles[i].show(p.mouseX, p.mouseY)
          particles[i].edges()

          for (let j = 0; j < particles.length; j++) {
            if (i !== j && particles[i].intersects(particles[j])) {
              particles[i].blast()
              particles[j].blast()

              for (let k = 0; k < 20; k++) {
                broken.push(new BrokenPeices(p, particles[i].position))
              }
            }
          }

          for (let j = broken.length - 1; j > 0; j--) {
            broken[j].show()
            if (broken[j].lifespan > 255) {
              broken.splice(j, 1)
            }
          }
        }
      }
    }
  }
  componentDidMount() {
    //console.log('mount')
    this.myP5 = new p5(this.sketch)
  }
  render() {
    return (
      <div id="home-wrapper" className="home-wrapper">
        <div ref={this.canvasRef} className="canvas-ref"></div>
        <div className="container align-middle">
          <div className="row mycustom-jumbotron">
            <div className="col-5">
              <img
                className="img-home"
                src="/images/DK_home_pic1.jpg"
                alt="DK"
                width="460"
                height="345"
              />
              <div>
                <b>Debajyoti Karmaker</b>
              </div>
            </div>
            <div className="col-7 text-right">
              <div className="link-container">
                <a href="https://debajyotikarmaker.github.io/">
                  <i className="fab fa-github fa-2x"></i>
                </a>
                <a href="/">
                  <i className="fab fa-google fa-2x ml-3"></i>
                </a>
                <a href="/">
                  <i className="fab fa-linkedin-in fa-2x ml-3"></i>
                </a>
                <a href="/">
                  <i className="fab fa-facebook fa-2x ml-3"></i>
                </a>
                <a href="/">
                  <i className="fab fa-skype fa-2x ml-3"></i>
                </a>
                <a href="/">
                  <i className="far fa-envelope-open fa-2x ml-3"></i>
                </a>
              </div>
              <p className="home-text">
                I am a Research Fellow at Australian National University (ANU),
                currently hosted by Dr. Hanna Kurniwati. Before joining ANU, I
                completed my Ph.D. from The University of Queensland (UQ) on
                September, 2019, under the supervision of Prof. Mandyam V.
                Srinivasan. My research interests are in computer vision,
                machine learning, and deep learning. I am particularly
                interested in the areas of collision avoidance strategies,
                Robust Decision-making and Learning, and Object detection.
                Before starting my PhD, I was working as a lecturer at the
                American International University-Bangladesh (AIUB) - in the
                department of Computer Science. I also worked as a software
                engineer at Infra Blue Technology (IBT Games).
              </p>
            </div>
          </div>
        </div>
      </div>
    )
  }
}

export default Home
