import React from 'react'

import ImageHolderBox from './ImageHolderBox'

const ResearchItems = ({ item }) => {
  const picList = item.images.map((pic) => <ImageHolderBox pic={pic} />)

  return (
    <div className="card">
      <div className="img-holder">{picList}</div>
      <div className="card-body">
        <h5 className="card-title align-middle">{item.title}</h5>
        <div className="publisher-name">{item.publisher}</div>
        <p className="card-text">{item.abstract}</p>
        <div className="d-flex  flex-wrap justify-content-between">
          <a
            href={item.url}
            target="_new"
            className="btn-sm btn-primary text-decoration-none"
          >
            View at publisher
          </a>
          <a
            href={item.projectDetails}
            className="btn-sm btn-primary text-decoration-none"
          >
            Project Details
          </a>
        </div>
      </div>
    </div>
  )
}

export default ResearchItems
