import p5 from 'p5'

class ParticleSystems {
  constructor(p) {
    this.p = p
    //console.log(p)
    this.position = p.createVector(p.random(p.width), p.random(p.height))
    this.velocity = p5.Vector.random2D()
    this.acceleration = p.createVector(0, 0)
    this.lifespan = p.random(100, 255)
    this.r = p.random(3, 5)
  }
  update() {
    this.lifespan += 0.5
    if (this.lifespan >= 255) {
      this.lifespan = this.p.random(100, 255)
      this.position = this.p.createVector(
        this.p.random(this.p.width),
        this.p.random(this.p.height)
      )
    }
    this.velocity.add(this.acceleration)
    this.position.add(this.velocity)
    this.acceleration.mult(0)
  }
  applyForce(force) {
    this.acceleration.add(force)
  }
  show(mouseX, mouseY) {
    this.p.stroke(this.lifespan)
    //this.p.fill(100)
    this.p.ellipse(this.position.x, this.position.y, this.r * 2, this.r * 2)
    if (
      this.p.abs(this.position.x - mouseX) < 100 &&
      this.p.abs(this.position.y - mouseY) < 100
    ) {
      this.p.line(mouseX, mouseY, this.position.x, this.position.y)
    }
  }
  edges() {
    if (this.position.x > this.p.width) this.position.x = 0
    if (this.position.x < 0) this.position.x = this.p.width
    if (this.position.y > this.p.height) this.position.y = 0
    if (this.position.x < 0) this.position.x = this.p.height
  }

  intersects(other) {
    var d = this.p.dist(
      this.position.x,
      this.position.y,
      other.position.x,
      other.position.y
    )
    if (d < this.r + other.r) {
      return true
    } else {
      if (d < 20) {
        this.p.line(
          other.position.x,
          other.position.y,
          this.position.x,
          this.position.y
        )
      }
      return false
    }
  }

  blast() {
    this.lifespan = 300
  }
}

export default ParticleSystems
